import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(a,s): any {
    let b=a
    if(s.trim())
    {
    	b=b.filter(item=>{
    		return Object.values(item).join("").includes(s)==true
    	})
    }

    return b
  }

}
